html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
  font-family: "Lexend", sans-serif;
}

img {
  max-width: 100%;
  display: block;
}

p {
  font-size: 2rem;
  margin: 0;
  padding: 0;
  color: #000000;
}

a {
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

li {
  list-style: none;
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

.img {
  width: 100%;
}

.section {
  padding: 2rem 0;
  overflow: hidden;
}

.notavif.notwebp .header {
  background-image: url(../img/page/header.jpg);
}

.webp .header {
  background-image: url(../img/page/header.webp);
}

.avif .header {
  background-image: url(../img/page/header.avif);
}

.header {
  min-height: 50rem;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 3rem 0 20rem 0;
}
.header__search {
  margin-top: 5rem;
  text-align: center;
}
.header__search-btn {
  transition: all 0.2s ease-in-out;
  font-size: 2rem;
  padding: 2rem 4rem;
  border-radius: 3rem;
  font-weight: 700;
  display: block;
  margin-top: 1rem;
  cursor: pointer;
  color: #e8505b;
  background-color: #ffffff;
}
.header__search-btn:focus-visible {
  outline: none;
}
@media (min-width: 768px) {
  .header__search-btn {
    display: inline-block;
  }
}
.header__search-btn:hover {
  color: #bd1e59;
  background-color: #e1e1e1;
}
@media (min-width: 768px) {
  .header__search {
    margin-top: 30rem;
  }
}

.bar__logo {
  width: 20rem;
}
@media (min-width: 768px) {
  .bar__logo {
    width: 10rem;
  }
}
.bar__nav {
  display: none;
}
@media (min-width: 768px) {
  .bar__nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 4rem;
  }
}
.bar__login {
  display: none;
}
@media (min-width: 768px) {
  .bar__login {
    display: block;
  }
}
.bar__link {
  transition: color 0.2s ease;
  color: #ffffff;
}
.bar__link:hover {
  color: #e8505b;
}
@media (min-width: 768px) {
  .bar {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 1rem;
  }
}

.form {
  margin: 5rem auto 2rem auto;
  padding: 3rem 0;
  border-radius: 5rem;
  overflow: hidden;
  box-shadow: 0px 3px 7px 0px #686868;
  background-color: #ffffff;
}
.form__field {
  padding: 1rem 2rem;
  transition: all 0.2s ease-in-out;
  margin-bottom: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: inherit;
  gap: 1rem;
}
@media (min-width: 1024px) {
  .form__field:nth-child(1) {
    padding-left: 4rem;
  }
}
.form__field:last-of-type {
  margin-bottom: 0;
}
.form__field:hover {
  background-color: #f0f0f0;
  border-radius: 5rem;
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.4);
}
@media (min-width: 1024px) {
  .form__field {
    margin-bottom: 0;
  }
}
.form__label {
  font-size: 1.2rem;
  font-weight: 900;
}
.form__input {
  border: none;
  width: 100%;
  padding: 0.5rem;
  color: #686868;
}
.form__input:focus-visible {
  outline: 0.3rem solid #e8505b;
}
.form__submit {
  display: block;
  height: 4rem;
  border: none;
  border-radius: 2rem;
  padding: 0 2rem;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.2rem;
  cursor: pointer;
  transition: all 0.2s ease;
  color: #ffffff;
  background-image: radial-gradient(circle, #ff385c 0%, #bd1e59 100%);
}
@media (min-width: 1024px) {
  .form__submit:hover, .form__submit:focus-visible {
    transform: scale(1.3);
  }
}
@media (min-width: 1024px) {
  .form {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 0;
    max-width: 100rem;
    padding: 0;
  }
}

.destinations__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .destinations__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .destinations__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}

.destiny {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  gap: 1rem;
}
.destiny__image-container {
  width: 14rem;
}

.places__grid {
  display: grid;
  grid-template-columns: repeat(4, 30rem);
  gap: 2rem;
  padding-bottom: 3rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
.places__grid > * {
  scroll-snap-align: center;
}
@media (min-width: 768px) {
  .places__grid {
    padding-bottom: 0;
    overflow: hidden;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .places__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}

.place {
  position: relative;
}
.place__img {
  border-radius: 1rem;
}
.place__name {
  font-size: 1.8rem;
  font-weight: bold;
  position: absolute;
  bottom: 1rem;
  left: 1rem;
  color: #bd1e59;
}
@media (min-width: 768px) {
  .place__name {
    position: inherit;
    bottom: unset;
    left: unset;
    margin-top: 1rem;
  }
}

.notwebp.notavif .host {
  background-image: linear-gradient(to bottom, #000000 0%, #000000 50%, transparent 50%), url("../img/page/anfitrion.jpg");
}
@media (min-width: 768px) {
  .notwebp.notavif .host {
    background-image: linear-gradient(to right, #000000 0%, #000000 45%, transparent 55%), url("../img/page/anfitrion.jpg");
  }
}

.webp .host {
  background-image: linear-gradient(to bottom, #000000 0%, #000000 50%, transparent 50%), url("../img/page/anfitrion.webp");
}
@media (min-width: 768px) {
  .webp .host {
    background-image: linear-gradient(to right, #000000 0%, #000000 45%, transparent 55%), url("../img/page/anfitrion.webp");
  }
}

.avif .host {
  background-image: linear-gradient(to bottom, #000000 0%, #000000 50%, transparent 50%), url("../img/page/anfitrion.avif");
}
@media (min-width: 768px) {
  .avif .host {
    background-image: linear-gradient(to right, #000000 0%, #000000 45%, transparent 55%), url("../img/page/anfitrion.avif");
  }
}

.host {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  row-gap: 15rem;
  padding: 5rem;
  background-repeat: no-repeat;
  background-size: 100%, 60rem;
  background-position: center, center bottom;
  margin-top: 4rem;
  color: #ffffff;
}
@media (min-width: 768px) {
  .host__content {
    max-width: 45%;
  }
}
.host__text {
  color: #ffffff;
}
.host__link {
  font-size: 2rem;
  padding: 2rem 4rem;
  border-radius: 3rem;
  font-weight: 700;
  display: block;
  margin-top: 1rem;
  cursor: pointer;
  color: #000000;
  background-color: #ffffff;
  border-radius: 1rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
  text-align: center;
  transition: all 0.2s ease-in-out;
}
.host__link:focus-visible {
  outline: none;
}
@media (min-width: 768px) {
  .host__link {
    display: inline-block;
  }
}
.host__link:hover {
  background-color: #e1e1e1;
}
@media (min-width: 480px) {
  .host {
    background-size: 100%, 72rem;
    background-position: center, center 200%;
  }
}
@media (min-width: 768px) {
  .host {
    padding: 15rem 5rem;
    grid-template-rows: unset;
    background-size: 100%, auto;
    background-position: center, 50% 30%;
  }
}
@media (min-width: 1024px) {
  .host {
    background-position: center, right center;
  }
}

.experiences__grid {
  display: grid;
  grid-template-columns: repeat(3, 30rem);
  gap: 2rem;
  padding-bottom: 3rem;
  margin: 2rem 0;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
.experiences__grid > * {
  scroll-snap-align: center;
}
@media (min-width: 768px) {
  .experiences__grid {
    padding-bottom: 0;
    overflow: hidden;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }
}

.experience {
  border-radius: 1rem;
  overflow: hidden;
}
.experience__description {
  background: linear-gradient(to right, #e8505b 0%, #bd1e59 100%);
  padding: 2rem 1rem;
}
.experience__description > * {
  color: #ffffff;
}
.experience__description h3 {
  font-size: 2rem;
}
.experience__description p {
  font-size: 1.6rem;
}

.footer {
  border-top: 0.1rem solid #cecece;
  background-color: #f3f3f3;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 1024px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.footer__widget {
  padding-bottom: 2rem;
  border-bottom: 0.1rem solid #cecece;
}
.footer__widget:last-of-type {
  border-bottom: 0;
}
@media (min-width: 1024px) {
  .footer__widget {
    border-bottom: 0;
  }
}
.footer__menu {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 2rem;
}
@media (min-width: 768px) {
  .footer__menu {
    flex-direction: row;
  }
}
@media (min-width: 1024px) {
  .footer__menu {
    flex-direction: column;
  }
}
.footer__heading {
  text-transform: uppercase;
  font-size: 1.2rem;
}
.footer__link {
  font-size: 1.4rem;
  transition: all 0.2s ease;
  color: #686868;
}
.footer__link--bar::before {
  content: "-";
  margin-right: 0.5rem;
}
.footer__link--bar:first-of-type::before {
  content: "";
}
.footer__link--bar:first-of-type:hover {
  text-decoration: none;
  color: #e8505b;
}
.footer__link:hover {
  text-decoration: underline;
  color: #e8505b;
}
.footer__bar {
  border-top: 0.1rem solid #cecece;
  padding: 2rem 0;
}
.footer__bar__nav {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .footer__bar__nav {
    flex-direction: row;
  }
}

.text-center {
  text-align: center;
}

.bg-primary {
  background-color: #e8505b;
}

.link-active {
  color: #bd1e59;
}

.line-divisor {
  display: none;
  height: 2.5rem;
  border-right: 1px solid #e1e1e1;
}
@media (min-width: 1024px) {
  .line-divisor {
    display: block;
  }
}

[class$=__heading] {
  margin: 2rem 0;
}